body {
  padding: 50px;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  display: flex;
}

.search-form input[type="text"] {
  width: 300px;
  border: 1px solid green;
  border-right: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: green;
  border: 1px solid green;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  font-size: 16px;
}

.search-form button[type="submit"]:disabled {
  cursor: auto;
  background-color: gray;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 50px;
  display: flex;
}

.photo-card {
  width: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.gallery__link {
  text-decoration: none;
  display: block;
}

.card-img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  display: block;
}

.info {
  background-color: #f0f0f0;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.info-item {
  margin-bottom: 10px;
}

.info-item b {
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.is-hidden {
  display: none;
}

